import React, { Component } from 'react';
import {skills} from '../shared/skills';
import Contact from './ContactComponent';
import Footer from './FooterComponent';
import Header from './HeaderComponent';
import About from './AboutComponent';
import Skill from './SkillComponent';
import Home from './HomeComponent';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import { Navbar, Nav, NavbarToggler, Collapse, NavItem} from 'reactstrap';


class  Main extends Component {
    constructor(props){
        super(props);
        this.state={
            skills:skills,
            isNavopen: false
        }
        this.toggleNav=this.toggleNav.bind(this);
    }

    toggleNav(){
        this.setState({
            isNavopen: !this.state.isNavopen
        })
    }

    componentDidMount() {

        Events.scrollEvent.register('begin', function () {
          console.log("begin", arguments);
        });
    
        Events.scrollEvent.register('end', function () {
          console.log("end", arguments);
        });
    
      }
      scrollTo() {
        scroller.scrollTo('scroll-to-element', {
          duration: 800,
          delay: 0,
          smooth: 'easeInOutQuart'
        })
      }
      scrollToWithContainer() {
    
        let goToContainer = new Promise((resolve, reject) => {
    
          Events.scrollEvent.register('end', () => {
            resolve();
            Events.scrollEvent.remove('end');
          });
    
          scroller.scrollTo('scroll-container', {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart'
          });
    
        });
    
        goToContainer.then(() =>
          scroller.scrollTo('scroll-container-second-element', {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart',
            containerId: 'scroll-container'
          }));
      }
      componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
      }
    render(){
        return(
            <div>
                <Navbar dark expand="md" className="fixed-top">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav}/>
                        <Collapse isOpen ={this.state.isNavopen} navbar>
                            <Nav navbar>
                                <NavItem><Link activeClass="active" className="nav-link linko"  offset={-60} to="home" spy={true} smooth={true} duration={500} ><span className="fa fa-home fa-lg"></span> Home</Link></NavItem>
                                <NavItem>
                                    <Link activeClass="active" className="nav-link linko"  offset={-60} to="about" spy={true} smooth={true} duration={500}><span className="fa fa-info fa-lg"></span> About</Link>
                                </NavItem>
                                <NavItem>
                                    <Link activeClass="active" className="nav-link linko"  offset={-60} to="skills" spy={true} smooth={true} duration={500}><span className="fa fa-list fa-lg"></span> Skills</Link>
                                </NavItem>
                                <NavItem>
                                    <Link activeClass="active" className="nav-link linko"  offset={-60} to="contact" spy={true} smooth={true} duration={500}><span className="fa fa-address-card fa-lg"></span> Feedback</Link>
                                </NavItem>
                                <NavItem>
                                    <a activeClass="active" className="nav-link" rel="noopener noreferrer" target="_blank" href="/Assets/Resume.pdf"><span className="fa fa-file fa-lg"></span> Resume</a>
                                </NavItem>
                            </Nav>
                            
                        </Collapse>
                    </div>
                </Navbar>
                <Element name="home" className="element" >
                    <Header/>
                    <Home/>
                </Element>
                <Element name="about" className="element" >
                    <About/>
                </Element>
                <Element name="skills" className="element" >
                    <Skill skills={this.state.skills} />
                </Element>
                <Element name="contact" className="element" >
                    <Contact/>
                </Element>
                
                <Footer/>
            </div>
            
            
        );
    }
    
}

export default Main;