import React from 'react';
import {Card, CardBody,CardImg,CardImgOverlay,CardText} from 'reactstrap';


function Home(props){
    return(
        <>
            <div className="container mt-4 mb-4">
                
                <Card>
                    <CardImg src="/Assets/back.jfif"/>
                    <CardImgOverlay className="d-flex ">
                        <CardText className=" text-center align-self-center mx-auto">
                            <h2>Once you stop learning, you start dying.</h2>
                            <p>--Albert Einstien</p>
                        </CardText>
                    </CardImgOverlay>
                </Card>
            </div>
        </>
    );
}

export default Home;