import React from 'react';
import {Card, CardBody} from 'reactstrap';

function Skill(props){
    var skills = props.skills;
    var renderSkill;
    
    renderSkill=skills.map((skill)=>{
        return(
            <div className="col-12 col-md-6 p-3" id={skill.id} >
                    <Card className="bg-primary">
                        <CardBody>
                            <div className="row">
                            <div className="col-12 col-md-6 align-self-center text-center text-white">
                            <h3>{skill.name}</h3>
                            </div>
                            <div className="col-12 col-md-6 text-center text-md-right" >
                            <img className=" skill-img" src={skill.img} alt={skill.name}></img>
                            </div>
                            </div>
                        </CardBody>
                        
                    </Card>
                </div>
            );
        });
    

    return(
        <div className="container mt-1 mb-4">
            <div className="row">
                <div className="col-12">
                    <h2>Skills</h2>
                </div>
            </div>
            <div className="row">
                {renderSkill}
            </div>
        </div>
    );
}
export default Skill;