import React from 'react';
import { Jumbotron} from 'reactstrap';

function Header(props){
    return(
        <>
            
                <Jumbotron>
                    <div className="container mb-4">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6 align-self-center text-sm-left text-center order-last order-md-first">
                                <h1>Gurcharan Kohli</h1>
                                <p>A Computer Guy</p>
                            </div>
                            <div className="col-12 col-sm-4 text-center order-first order-md-last ">
                                <img src="/Assets/1.jpg" className="rounded-circle img-thumbnail" height="250" width="250"></img>
                            </div>
                        </div>

                    </div>
                </Jumbotron>
            </>
        );
}

export default Header;