import React,{Component} from 'react';
import {Form, FormGroup, Label, Input, Button, Row, Col, Card, CardBody} from 'reactstrap';

class Contact extends Component{
    constructor(props){
        super(props);
        this.state={
            status: ""
        };
        this.submitForm=this.submitForm.bind(this);
    }

    render(){
        const {status} = this.state;
        return(
            <div className="container mt-10">
                <h2>Feedback Form</h2>
                
                <Form onSubmit={this.submitForm} action="https://formspree.io/f/xgepokjl" method="POST" className="mt-4">
                    <Row className="form-group" >
                        <Label htmlFor="name" md={1}>Name</Label>
                        <Col md={10}>
                            <Input type="text" name="name" md={10}></Input>
                        </Col>
                    </Row>
                    <Row className="form-group">
                        <Label htmlFor="email" md={1}>Email</Label>
                        <Col md={10}>
                            <Input type="email" name="email" md={10}></Input>
                        </Col>
                    </Row>
                    <Row className="form-group" >
                        <Label htmlFor="feedback" md={1}>Feedback</Label>
                        <Col md={10}>
                            <Input type="textarea" name="feedback" md={10} rows="12"></Input>
                        </Col>
                    </Row>
                    <Row className="form-group">
                        <Col md={1}>

                        </Col>
                        <Col md={10}>
                            <Button type="submit" color="primary">Send Feedback</Button>
                        </Col>
                    </Row>
                   
                </Form>
                <Row>
                    <Col md={1}>

                    </Col>
                    <Col md={10}>
                        {status === ""? <div></div> : <p color="#1ACF24">{status}</p>}
                    </Col>
                
                </Row>
            </div>
            
        );
    }

    submitForm(ev) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
          if (xhr.readyState !== XMLHttpRequest.DONE) return;
          if (xhr.status === 200) {
            form.reset();
            this.setState({ status: "Thanks for your feedback!" });
          } else {
            this.setState({ status: "ERROR! Could not submit feedback" });
          }
        };
        xhr.send(data);
      }
}

export default Contact;